.message__text {
  height: 120px;
}
.like {
  position: relative;
  top: 154px;
  margin-right: 36px;
  padding: 20px;
  width: 30px;
  height: 30px;
  background-color: white;
  background-image: url("/build/img/like.svg");
  background-repeat: no-repeat;
  border: none;
  outline: none;
  border-radius: 100%;
}
.like:active {
  transform: translateY(-1px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  transform: scaleX(1.1) scaleY(1.1);
  margin-right: 39px;
  opacity: 1;
}
.like:hover {
  opacity: 0.8;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}
.like-counter {
  position: relative;
  top: 55px;
  right: 22px;
}
.count__like {
  position: relative;
  border: none;
  top: -6px;
  width: 30px;
  height: 30px;
  padding-left: 30px;
}
.lot__image {
  display: flex;
  max-width: 550px;
}
.parent-del {
  width: 2px;
  padding: 0;
  margin: 0;
}
.delete_hotel {
  position: relative;
  z-index: 12;
  top: -26px;
  left: -10px;
  margin: 0 auto;
  max-width: 30px;
}
.dop-del {
  position: relative;
  top: -55px;
  padding: 7px;
  padding-bottom: 4px;
  left: -7px;
  height: 6px;
  z-index: 10;
  font-size: 10;
  color: white;
  background-color: white;
}
.hotel-image {
  z-index: 2;
}
.lot__state {
  position: relative;
  min-height: 200px;
}
.lots-title {
  margin-top: 40px;
  font-family: 'Geneva';
  font-size: 22px;
  text-shadow: 2px 2px 4px #7e7e7e;
  text-transform: uppercase;
}
.lot__text {
  padding: 5px;
  margin: 0;
}
.lot__category {
  padding-top: 15px;
}
.lot__title {
  padding-top: 16px;
  font-size: 22px;
  font-weight: bold;
  font-weight: 700;
}
.lot__button {
  position: absolute;
  color: white;
  margin-top: 10px;
  padding: 15px;
  background: #e24a4a;
  border-radius: 10px;
}
.delete-form__hotel {
  display: flex;
  justify-content: space-around;
}
.delete_button {
  padding-left: 30px;
  padding-right: 30px;
}
.delete__close {
  padding-left: 30px;
  padding-right: 30px;
}
.delete-form__close {
  margin-top: 15px;
}
